<template>
  <div>
    <a-modal
      v-model="dialogNowMeetingTable"
      centered
      title="การใช้งานห้องประชุมปัจจุบัน"
    >
      <a-spin :spinning="spinning">
        <div>
                  <a-empty  v-show="this.empty_data == true"  style="margin-top: 20px" >
<span slot="description" > ขณะนี้ไม่มีผู้ใช้งานห้องประชุม </span>
           </a-empty>
          <v-row
            class="pt-5"
            align="center"
            justify="center"
            v-for="items in this.data_nowMeeting"
            :key="items"
          >

            <v-col class="pa-0">
  
              <a-card>
                <a-timeline>
                      <a-timeline-item color="blue">
                    <a-icon
                      class="pr-5"
                      slot="dot"
                      type="solution"
                      style="font-size: 18px;"
                    />
                    {{ "หัวข้อประชุม : " + items.agenda }}
                  </a-timeline-item>

                <a-timeline-item color="blue">
                    <a-icon slot="dot" type="bank" style="font-size: 18px;" />
                    {{ "ห้องประชุมที่ : " + items.room_num }}
                  </a-timeline-item>

                  <a-timeline-item color="blue">
                    <a-icon
                      slot="dot"
                      type="calendar"
                      style="font-size: 18px;"
                    />
                    {{ parseToDateFormat(items.meeting_start) }}
                  </a-timeline-item>

              
                  <a-timeline-item color="blue">
                    <a-icon
                      slot="dot"
                      type="clock-circle-o"
                      style="font-size: 18px;"
                    />
                    {{
                      parseToTimeFormat(items.meeting_start, items.meeting_end)
                    }}
                  </a-timeline-item>

                </a-timeline>

              </a-card>
            </v-col>
          </v-row>
        </div>
      </a-spin>

      <template slot="footer">
        <a-button
          key="back"
          type="primary"
          @click="close_dialogNowMeetingTable()"
        >
          ปิด
        </a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
export default {
  components: {},
  data: () => ({
    dialogNowMeetingTable: false,
    user_login: "",
    data_nowMeeting: null,
    spinning: false,
    modal_eject: null,
    empty_data: false,
    email_user:"",
  }),
  created() {
    this.getdata_storage();
  },
  methods: {
    open() {
      this.dialogNowMeetingTable = true;
      this.API_nowMeetingTable();
    },
    close_dialogNowMeetingTable() {
      this.dialogNowMeetingTable = false;
    },
    cleanSpinning() {
      this.spinning = false;
    },
    API_nowMeetingTable(){
        this.spinning = true;
       
        var resdata = ""
        this.axios({
        method: "GET",
        url: this.$store.state.url_get_API_axios + "api/v1/nowMeetingTable",
        // headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          console.log("API_nowMeetingTable--->",response);

          this.results = response.data;
          this.cleanSpinning();
          console.log("---->",typeof(this.results.Value.length));
          if (this.results.Status == "success") {
            this.data_nowMeeting = this.results.Value;
             this.empty_data = false
              if(this.results.Value.length == 0){
                this.data_nowMeeting = [];
                this.empty_data = true
              }
          } 
  
          else {
            this.cleanSpinning();
            resdata = "ไม่พบข้อมูล"
            this.Model_countDown_error(resdata)

          }
        })
        .catch((error) => {
            this.cleanSpinning();
          console.log(error.message);
          // this.loading = false;
          // if (error.message == "Request failed with status code 400") {
          //   this.$message.error("ไม่พบข้อมูล");
          // }
        });
    },
    
    parseToDateFormat(dateString) {
      let date = new Date(dateString);

    if (date.getDate() < 10 ||  (date.getMonth() + 1) < 10) {
        var dd = "0" + date.getDate();
        var mm = "0" + (date.getMonth() + 1);
      }
      return (
        "วันที่ : " +
        dd+
        " - " +
        mm +
        " - " +
        date.getFullYear()
      );
    },
    parseToTimeFormat(startTime, endTime) {
      let timeStart = new Date(startTime);
      let timeEnd = new Date(endTime);
      var timeStart_min = 0;
      var timeEnd_min = 0;
      if (timeStart.getMinutes() == 0) {
        timeStart_min = timeStart.getMinutes() + "0";
      } else {
        timeStart_min = timeStart.getMinutes();
      }
      if (timeEnd.getMinutes() == 0) {
        timeEnd_min = timeEnd.getMinutes() + "0";
      } else {
        timeEnd_min = timeEnd.getMinutes();
      }
      return (
        "เวลา : " +
        timeStart.getHours() +
        ":" +
        timeStart_min +
        " - " +
        timeEnd.getHours() +
        ":" +
        timeEnd_min +
        " น."
      );
    },
    getdata_storage() {
      let data = JSON.parse(window.localStorage.getItem("user_data"));

      if (data === null) {
        this.getdata = null;
      } else {
        this.email_user = data.email;
      }
    },
     Model_countDown_error(response_Msg) {
      if (response_Msg == "fail") {
        let secondsToGo = 3;
        const modal = this.$error({
          title: "ไม่พบผู้ใช้นี้",
          content: `โปรดล็อกอินอีกครั้ง`,
          centered: true,
        });
        const interval = setInterval(() => {
          secondsToGo -= 1;
          modal.update({
            content: `โปรดล็อกอินอีกครั้ง`,
          });
        }, 1000);
        setTimeout(() => {
          clearInterval(interval);
          modal.destroy();
        }, secondsToGo * 1000);
      } 
      else if(response_Msg == "Request failed with status code 400"){
 let secondsToGo = 3;
        const modal = this.$error({
          title: "กรุณากรอกข้อมูลให้ครบถ้วน",
          content: `เนื่องจากกรอกข้อมูลไม่ครบถ้วน`,
          centered: true,
        });
        const interval = setInterval(() => {
          secondsToGo -= 1;
          modal.update({
            content: `เนื่องจากกรอกข้อมูลไม่ครบถ้วน`,
          });
        }, 1000);
        setTimeout(() => {
          clearInterval(interval);
          modal.destroy();
        }, secondsToGo * 1000);
      }
      else {
        let secondsToGo = 3;
        const modal = this.$error({
          title: "เกิดข้อผิดพลาด",
          content: `ไม่สามารถใช้งานได้`,
          centered: true,
        });
        const interval = setInterval(() => {
          secondsToGo -= 1;
          modal.update({
            content: `ไม่สามารถใช้งานได้`,
          });
        }, 1000);
        setTimeout(() => {
          clearInterval(interval);
          modal.destroy();
        }, secondsToGo * 1000);
      }
    },
  },
};
</script>
<style scoped></style>
